import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { AppSettingsService } from '../include/app-settings/app-settings.service';
import { GoogleAnalyticsService } from '../include/google-analytics/google-analytics.service';

@Component({
    selector: 'app-about',
    templateUrl: './about.page.html',
    styleUrls: ['./about.page.scss'],
})
export class AboutPage implements OnInit {

    constructor (
        private ga: GoogleAnalyticsService,
        private settings: AppSettingsService,
        private nav: Router,
    ) { }

    ios: boolean;
    android: boolean;

    ngOnInit() {
    }

    ionViewWillEnter() {
        this.ga.pageView(this.nav.url);

        if (this.settings.usingIOS) {
            this.ios = true;
        } else if (this.settings.usingAndroid) {
            this.android = true;
        } else {
            this.ios = true;
            this.android = true;
        }
    }
}
